import { Component, Input, ChangeDetectorRef, OnInit, OnDestroy } from '@angular/core';
import { ExtendedUrlImg } from "../../extended/extendedurlimg";
import { NavController } from "ionic-angular";
import { ChannelProvider } from "../../providers/channel/channel";


@Component({
  selector: 'airdetector',
  templateUrl: 'airdetector.html'
})
//空气检测仪
export class AirdetectorComponent implements OnInit, OnDestroy {
  //设备信息
  deviceinfo: any;
  src: string = "";
  pm25: string = "";
  type: string = "";
  subonline: any = null;
  airValueChange: any = null;
  constructor(private navCtrl: NavController,
    private extimg: ExtendedUrlImg,
    private ch: ChannelProvider,
    private ref: ChangeDetectorRef) {

  }
  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    if (!this.deviceinfo.onlinestatus) {
      this.pm25 = "设备离线";
    } else {
      if (this.deviceinfo.latest_staus != null) {
        if (this.deviceinfo.latest_staus.param.pM25 != null)
          this.pm25 = "PM2.5  " + this.deviceinfo.latest_staus.param.pM25 + "μg/m³";
        else
          this.pm25 = "设备在线";
      } else
        this.pm25 = "设备在线";
    }


    this.src = this.extimg.getlist_64(this.deviceinfo.product_id);
  }

  ngOnInit() {
    this.ref.markForCheck();
    if (this.subonline === null)
      this.subonline = this.ch.sub("online").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          if (data.data.online) {
            if (this.deviceinfo.latest_staus.param.pM25 != null)
              this.pm25 = "PM2.5  " + this.deviceinfo.latest_staus.param.pM25 + "μg/m³";
            else
              this.pm25 = "设备在线";
          }
          else
            this.pm25 = "设备离线";
          this.ref.detectChanges();
        }
      });
    if (this.airValueChange === null)
      this.airValueChange = this.ch.sub("airValueChange").subscribe(data => {
        console.log(data);
        if (data.data.device_id === this.deviceinfo.device_id && data.data.services.type === 216) {
          this.pm25 = "PM2.5  " + data.data.services.valve + "μg/m³";
          this.ref.detectChanges();
        }
      });
  }
  ngOnDestroy() {
    this.ref.detach();
    this.subonline.unsubscribe();
  }


  @Input() set Type(type: string) {
    this.type = type;
  }
  pushDPage() {
    this.navCtrl.push('airdetector', { device_id: this.deviceinfo.device_id });
  }
}



