import { Component, Input, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
//import { DeviceConst } from "../../interfaces/deviceconst";
import { LechengProvider } from '../../providers/lecheng/lecheng';
import { LocaldataProvider } from "../../providers/localdata/localdata";

@Component({
  selector: 'demoncamera',
  templateUrl: 'demoncamera.html'
})
export class DemoncameraComponent implements OnInit, OnDestroy {

  deviceinfo: any;//设备信息
  type: string;//显示的模式 0列表 1九宫格 2老人
  online: string;//设备是否在线 
  deviceproduct: string;//设备类型
  isgroup: boolean = false;//是否是组设备
  isload:boolean = false;
  userToken:string = "";
  title:string = "";
  constructor(
    private lechengService: LechengProvider,
    //private ch: ChannelProvider,
    private ref: ChangeDetectorRef,
    private localDataService: LocaldataProvider) {

  }

  @Input() set Type(type: string) {
    this.type = type;
  }

  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    this.deviceproduct = this.deviceinfo.product_id;
    this.isgroup = this.deviceinfo.ingroup === 2;
    this.title = this.deviceinfo.name;
  }

  ngOnInit() {
    this.ref.markForCheck();
    this.isload = false;
    this.lechengService.GetDeviceToken(this.deviceinfo.device_id).subscribe(result => {
        
        if (result.error_code==0) { 
            this.online = result.data.isOnline; //在线状态
            this.userToken = result.data.userToken;
            
            this.isload = true;
        } else {
          this.localDataService.alert("提示", "初始化视频失败:" + result.error_msg);
        }
        
    }, error => {
      this.localDataService.alert("提示", "初始化视频失败");
    });
    
  }

  OpenVideo() {
    if(!this.isload) {
      this.localDataService.alert("提示", "视频尚未初始化完成,请稍后");
      return;
    }
    let options:mixinOptions = {
        user_token: this.localDataService.getToken(),
        device_id: this.deviceinfo.device_id,
        channle_id: '0',
        action: "play",
        fileId: "",
        recordID: "",
        type:1,
        title:this.title
    };
    cordova.plugins.lcOpenSDK.openVideo(this.userToken,this.deviceinfo.sn,1, options, (result) => {
        this.openVideoSuccess(result);

    },
    (result) => {
        
        this.localDataService.alert("提示", "实时播放失败:" + result.error_msg);
    });
  }

  openVideoSuccess(code) {
    
  }

  ngOnDestroy() {
    this.ref.detach();
  }
}



