import { Component, Input, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { NavController } from 'ionic-angular';
import { DeviceConst } from '../../interfaces/deviceconst';
import { ExtendedUrlImg } from '../../extended/extendedurlimg';
import { EquipmentProvider } from '../../providers/equipment/equipment';
import { ChannelProvider } from '../../providers/channel/channel';


/**支持的所有设备
 * wifi 窗帘
 * wifi 小圆插
 * 空气质量检测仪
 * 红外万能摇控器
 * 射频网关
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'wifidevice-device',
  templateUrl: 'wifidevice.html'
})

//wifi设备Component
export class WifideviceComponent implements OnInit, OnDestroy {
  deviceinfo: any;//设备信息
  type: string;//显示的模式 0列表 1九宫格 2老人
  online: string;//设备是否在线 
  devicetype: string;//设备类型
  src: string;//列表图片路径
  isgroup: boolean = false;//是否是组设备
  subonline: any = null;
  subdeviceState: any = null;
  updateDeviceName: any = null;
  //wifi小圆插
  wifioutlet: string = DeviceConst.WF_CIRCLE_OUTLET;
  checked: boolean = false;//当前状态
  oldchecked: boolean = false;//上一次状态
  stattype: boolean = false;//按钮不能一直点

  //wifi窗帘
  wificurtain: string = DeviceConst.CURTAIN;
  buttonstatus1: string = "in-open";
  buttonstatus2: string = "in-stop";
  buttonstatus3: string = "in-close";
  brightness: number = 0;
  oldbrightness: number = 0;
  constructor(private navCtrl: NavController,
    private extimg: ExtendedUrlImg,
    private eq: EquipmentProvider,
    private ch: ChannelProvider,
    private ref: ChangeDetectorRef) {
  }

  @Input() set Type(type: string) {
    this.type = type;
  }


  //页面加载时运行。此事件仅在每个页面创建时发生一次。如果页面离开但已被缓存，则此事件将不会在后续查看时再次触发。该ionViewDidLoad事件是放置页面的设置代码的好地方。
  ionViewDidLoad() {

  }
  ngOnInit() {
    this.ref.markForCheck();
    if (this.subonline === null)
      this.subonline = this.ch.sub("online").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          if (data.data.online) {
            this.deviceTypeJudgment(1);
          }
          else
            this.online = "设备离线";
          this.ref.detectChanges();
        }
      });
    if (this.subdeviceState === null)
      this.subdeviceState = this.ch.sub("deviceState").subscribe(data => {
        if (this.deviceinfo.ingroup === 1) return;
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.deviceTypeJudgment(2, data);
          this.ref.detectChanges();
        }
      });
    if (this.updateDeviceName === null)
      this.updateDeviceName = this.ch.sub("updateDeviceName").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.deviceinfo.name = data.data.name;
          this.ref.detectChanges();
        }
      });
  }
  ngOnDestroy() {
    this.ref.detach();
    this.subonline.unsubscribe();
    this.subdeviceState.unsubscribe();
    this.updateDeviceName.unsubscribe();
  }

  change(event: any) {
    if (event.value === this.oldbrightness) return;
    //let sum = this.oldbrightness;
    this.brightness = event.value;
    this.eq.Send(this.deviceinfo.device_id, 9998, this.brightness).subscribe(data => {
    });
  }


  deviceTypeJudgment(operation: number, data: any = null) {
    switch (this.devicetype) {
      case DeviceConst.RF_GATEWAY:
        {
          switch (operation) {
            case 1:
              this.online = "设备在线";
              break;
            case 3:
              this.navCtrl.push('rfgateway', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.WF_CIRCLE_OUTLET://wifi小圆插
        {
          switch (operation) {
            case 0:
              if (this.deviceinfo.latest_staus != null) {
                this.checked = this.deviceinfo.latest_staus.port_on_off[0];
                this.oldchecked = this.deviceinfo.latest_staus.port_on_off[0];
              }
              break;
            case 1:
              this.online = "设备在线";
              break;
            case 2:
              this.stattype = false;
              if (this.deviceinfo.latest_staus === null)
                this.deviceinfo.latest_staus = outlet_status;
              this.deviceinfo.latest_staus.port_on_off = data.data.services.port_on_off;
              this.checked = this.deviceinfo.latest_staus.port_on_off[0];
              this.oldchecked = this.deviceinfo.latest_staus.port_on_off[0];
              break;
            case 3:
              this.navCtrl.push('circleoutle', { 'device_id': this.deviceinfo.device_id });
              break;
          }
        }
        break;
      case DeviceConst.Airradio_A1://空气电台
      case DeviceConst.Airradio_A2:
      case DeviceConst.Airradio_A2se:
        {
          switch (operation) {
            case 0:
              if (this.deviceinfo.latest_staus != null) {
                if (this.deviceinfo.latest_staus.isOnline === 0) {
                  this.online = "设备离线";
                } else {
                  if (this.deviceinfo.latest_staus.param.pM25 != null)
                    this.online = "PM2.5  " + this.deviceinfo.latest_staus.param.pM25 + "μg/m³";
                  else
                    this.online = "设备在线";
                }
              }
              break;
            case 1:
              if (this.deviceinfo.latest_staus.param.pM25 != null)
                this.online = "PM2.5  " + this.deviceinfo.latest_staus.param.pM25 + "μg/m³";
              else
                this.online = "设备在线";
              break;
            case 3:
              this.navCtrl.push('airradio', { 'device_id': this.deviceinfo.device_id });
              break;
          }
        }
        break;
      case DeviceConst.IR_GATEWAY://红外万能摇控器
        {
          switch (operation) {
            case 1:
              this.online = "设备在线";
              break;
            case 3:
              this.navCtrl.push('manage_ir_gateway', { 'device_id': this.deviceinfo.device_id });
              break;
          }
        }
        break;
      case DeviceConst.CURTAIN://wifi窗帘
        {

          // this.brightness = this.oldbrightness;
          switch (operation) {
            case 0:
              this.oldbrightness = (this.deviceinfo.latest_staus !== null && this.deviceinfo.latest_staus.window_position !== null) ? this.deviceinfo.latest_staus.window_position : 0;
              break;
            case 2:
              if (this.deviceinfo.ingroup === 1) return;
              if (data.data.services.window_position === -1) return;
              this.deviceinfo.latest_staus.window_position = data.data.services.window_position;
              this.deviceinfo.latest_staus.window_status = data.data.services.window_status;
              this.brightness = data.data.services.window_position;
              this.oldbrightness = this.deviceinfo.latest_staus.window_position;
              break;
            case 1:
              this.online = "设备在线";
              break;
            case 3:
              this.navCtrl.push('curtain', { 'device_id': this.deviceinfo.device_id });
              break;
          }
        }
        break;
      case DeviceConst.TC1_CAMERA:
      case DeviceConst.TP1_CAMERA:
        switch (operation) {
          case 1:
            this.online = "设备在线";
            break;
          case 3:
            this.navCtrl.push('camera', { 'device_id': this.deviceinfo.device_id, 'file_id': '0' });
            break;
        }
        break;
      case DeviceConst.ZWAVE_GATEWAY:
        switch (operation) {
          case 1:
            this.online = "设备在线";
            break;
          case 3:
            this.navCtrl.push('zwavegateway', { 'device_id': this.deviceinfo.device_id });
            break;
        }
        break;
    }
  }

  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    this.devicetype = this.deviceinfo.product_id;
    this.src = this.extimg.getlist_64(this.deviceinfo.product_id);
    this.isgroup = this.deviceinfo.ingroup === 2;
    this.online = this.deviceinfo.onlinestatus ? "设备在线" : "设备离线";
    this.deviceTypeJudgment(0);
  }

  /*****************进入详细页面******************* */
  pushPage() {
    this.deviceTypeJudgment(3);
  }

  /***************wifi小圆插开关按钮*************** */
  clickvalue() {
    if (this.stattype) {
      this.checked = this.checked ? false : true;
      return
    };
    this.stattype = true;
    if (this.deviceinfo.latest_staus != null) {
      let on_off = this.deviceinfo.latest_staus.port_on_off[0] ? 0 : 1;
      this.eq.Send(this.deviceinfo.device_id, on_off).subscribe(data => {
        setTimeout(() => {
          if (this.stattype) {
            this.stattype = false;
            this.checked = this.oldchecked;
          }
        }, 2000);
      });;
    } else {
      this.eq.Send(this.deviceinfo.device_id, 1).subscribe(data => {
        setTimeout(() => {
          if (this.stattype) {
            this.stattype = false;
            this.checked = this.oldchecked;
          }
        }, 2000);
      });;
    }
  }

  /***************wifi窗帘*************** */
  click1() {
    if (this.buttonstatus1 === "in-login") return;
    this.setTimeOut(3);
    this.eq.Send(this.deviceinfo.device_id, 1).subscribe(data => {
    });
  }
  click2() {

    if (this.buttonstatus1 === "in-login") return;
    this.setTimeOut(2);
    this.eq.Send(this.deviceinfo.device_id, 3).subscribe(data => {
    });
  }
  click3() {
    if (this.buttonstatus1 === "in-login") return;
    this.setTimeOut(1);
    this.eq.Send(this.deviceinfo.device_id, 2).subscribe(data => {
    });
  }

  //wifi超时返回原状态
  setTimeOut(type: number) {
    switch (type) {
      case 1:
        this.buttonstatus1 = "in-login";
        setTimeout(() => {
          if (this.buttonstatus1 === "in-login") {
            this.buttonstatus1 = "in-open";
          }
        }, 1000);
        break;
      case 2:
        this.buttonstatus2 = "in-login";
        setTimeout(() => {
          if (this.buttonstatus2 === "in-login") {
            this.buttonstatus2 = "in-stop";
          }
        }, 1000);
        break;
      case 3:
        this.buttonstatus3 = "in-login";
        setTimeout(() => {
          if (this.buttonstatus3 === "in-login") {
            this.buttonstatus3 = "in-close";
          }
        }, 1000);
        break;
    }
  }
}
export class outlet_status {
  port_on_off: Array<boolean>;

  port_in_use: Array<boolean>;
}


