import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { SwitchComponent } from './switch';
import { ExtendedOutlet } from './extendedoutlet';
import { SwitchrounddelayComponent } from "./switchrounddelay";
import { SwitchButtonDetailed } from "./switch.detailed.component";
import { SwitchdetailedaComponent } from "./switchdetaileda";
import { GradientComponent } from "./gradient.component";
import { ColorPickerComponentModule } from '../colorpicker/lib/color-picker.module';
@NgModule({
  declarations: [
    SwitchComponent,
    SwitchrounddelayComponent,
    SwitchButtonDetailed,
    SwitchdetailedaComponent,
    GradientComponent

  ],
  imports: [
    IonicModule,
    ColorPickerComponentModule
  ],
  exports: [
    SwitchComponent,
    SwitchrounddelayComponent,
    SwitchButtonDetailed,
    SwitchdetailedaComponent,
    GradientComponent
  ], providers: [ExtendedOutlet]
})
export class SwitchComponentModule { }



