import { Component, Input } from '@angular/core';

@Component({
    selector: "switch-button-detailed",
    templateUrl: "./switch.detailed.component.html",
})
//开关按钮样式状态
export class SwitchButtonDetailed {
    // outline ion-button icon-only
    round_switch_button_status: string;
    name: string;
    //type: number; 1//一开 2二开 3三开
    @Input() set myroundstatus(roundstatus: string) {
        this.round_switch_button_status = roundstatus;
    }

    @Input() set myname(name: string) {
        this.name = name;
    }

    // @Input() set mytype(type: number) {
    //     this.type = type;
    // }
}


