import { Injectable } from '@angular/core';
//import { OutletModel } from '../../model/shebei/outletmodel';
import { EquipmentProvider } from '../../providers/equipment/equipment';
import { DeviceConst } from '../../interfaces/deviceconst';
import { LocaldataProvider } from '../../providers/localdata/localdata';

@Injectable()
export class ExtendedOutlet {
    constructor(private eq: EquipmentProvider,
        private ls: LocaldataProvider) {
    }
    devname: string;
    refresh(deviceinfo: any, model: any) {

        if (deviceinfo.latest_staus != null) {
            let status = deviceinfo.latest_staus.port_on_off;
            for (let i = 0; i < status.length; i++) {
                if (deviceinfo.onlinestatus) {
                    if (i === 0) {
                        if (status[i] === false)
                            model.buttonstatus1 = "in-close";
                        else
                            model.buttonstatus1 = "in-open";
                    } else if (i === 1) {
                        if (status[i] === false)
                            model.buttonstatus2 = "in-close";
                        else
                            model.buttonstatus2 = "in-open";
                    } else if (i === 2) {
                        if (status[i] === false)
                            model.buttonstatus3 = "in-close";
                        else
                            model.buttonstatus3 = "in-open";
                    }
                } else {
                    if (i === 0) {
                        model.buttonstatus1 = "in-disconnect";//离线
                    } else if (i === 1) {
                        model.buttonstatus2 = "in-disconnect";
                    } else if (i === 2) {
                        model.buttonstatus3 = "in-disconnect";
                    }
                }
            }
        }
        if (deviceinfo.device_subinfo != null && deviceinfo.device_subinfo.items != null) {
            let names = deviceinfo.device_subinfo.items;
            if (names != null && names.length > 0) {
                for (let i = 0; i < names.length; i++) {
                    if (i === 0) {
                        model.name1 = names[0];
                    } else if (i === 1) {
                        model.name2 = names[1];
                    } else if (i === 2) {
                        model.name3 = names[2];
                    }
                }
            }
        } else {
            model.name1 = "开关1";
            model.name2 = "开关2";
            model.name3 = "开关3";
        }
    }

    //超时返回原状态
    setTimeOut(model: any, val: number, type: number) {
        switch (type) {
            case 0:
                model.buttonstatus1 = "in-login";
                setTimeout(() => {
                    if (model.buttonstatus1 === "in-login") {
                        model.buttonstatus1 = (val === 1) ? "in-close" : "in-open";
                    }
                }, 5000);
                break;
            case 1:
                model.buttonstatus2 = "in-login";
                setTimeout(() => {
                    if (model.buttonstatus2 === "in-login") {
                        model.buttonstatus2 = (val === 1) ? "in-close" : "in-open";
                    }
                }, 5000);
                break;
            case 2:
                model.buttonstatus3 = "in-login";
                setTimeout(() => {
                    if (model.buttonstatus3 === "in-login") {
                        model.buttonstatus3 = (val === 1) ? "in-close" : "in-open";
                    }
                }, 5000);
                break;
        }
    }
    // index 开关的第几个按键 type true已反溃还是false刚按下
    getOutletName(device: any, index: number, type: boolean): string {
        if (device.latest_staus === null) {
            return "打开中";
        } else {
            if (device.device_subinfo != null) {
                let title = type ? (device.latest_staus.port_on_off[index]) ? "已打开" : "已关闭" : (device.latest_staus.port_on_off[index]) ? "关闭中,请稍后..." : "打开中,请稍后...";
                return device.device_subinfo.items[index] + title;
            } else {
                let title = type ? (device.latest_staus.port_on_off[index]) ? "已打开" : "已关闭" : (device.latest_staus.port_on_off[index]) ? "关闭中,请稍后..." : "打开中,请稍后...";
                return title;
            }
        }


    }

    // index 表示第几个按钮 详细页面开关按钮
    switchClick(deviceinfo: any, model: any, counterChange: any, index: number) {
        {
            let status: number = 0;
            this.devname = this.getOutletName(deviceinfo, index, false);
            if (index === 0 && model.buttonstatus1 === "in-login") {
                counterChange.emit({
                    title: this.devname,
                    type: false
                });
                return;
            }
            if (index === 1 && model.buttonstatus2 === "in-login") {
                counterChange.emit({
                    title: this.devname,
                    type: false
                });
                return;
            }
            if (index === 2 && model.buttonstatus3 === "in-login") {
                counterChange.emit({
                    title: this.devname,
                    type: false
                });
                return;
            }
            if (deviceinfo.ingroup === 2) {
                if (deviceinfo.latest_staus != null) {
                    status = (deviceinfo.latest_staus.port_on_off[index]) ? 0 : 1;
                    this.switchClickSend(deviceinfo, model, counterChange, index, status);
                } else//如果不存在读取组设备第一条数据
                {
                    let groupdevideid = deviceinfo.group_devices[0];
                    this.eq.GetFindDevicesByDeviceId(groupdevideid).subscribe(data => {
                        if (data.data.latest_staus != null) {
                            status = (deviceinfo.latest_staus.port_on_off[index]) ? 0 : 1;
                            this.switchClickSend(deviceinfo, model, counterChange, index, status);
                        }
                    });
                }
            } else {
                status = (deviceinfo.latest_staus === null) ? 0 : (deviceinfo.latest_staus.port_on_off[index] === false) ? 1 : 0;
                this.switchClickSend(deviceinfo, model, counterChange, index, status);
            }
        }
    }
    //列表开关按钮
    listSwitchClick(deviceinfo: any, model: any, index: number) {
        {
            //获取当前第一个按钮的状态 0关， 1开,2不控制
            let status: number = 0;
            if (index === 0 && model.buttonstatus1 === "in-login") {
                //this.ls.showMessage("打开中,请稍后...");
                return;
            }
            if (index === 1 && model.buttonstatus2 === "in-login") {
                // this.ls.showMessage("打开中,请稍后...");
                return;
            }
            if (index === 2 && model.buttonstatus3 === "in-login") {
                //this.ls.showMessage("打开中,请稍后...");
                return;
            }
            if (deviceinfo.ingroup === 2)//设备组设备
            {
                if (deviceinfo.latest_staus != null) {
                    status = (deviceinfo.latest_staus.port_on_off[index] === false) ? 1 : 0;
                    this.listSwitchClickSend(deviceinfo, model, index, status);
                } else//如果不存在读取组设备第一条数据
                {
                    let groupdevideid = deviceinfo.group_devices[0];
                    this.eq.GetFindDevicesByDeviceId(groupdevideid).subscribe(data => {
                        if (data.data.latest_staus != null)
                            status = (data.data.latest_staus.port_on_off[index]) ? 0 : 1;
                        else
                            status = 0;
                        this.listSwitchClickSend(deviceinfo, model, index, status);
                    });
                }
            } else {
                status = (deviceinfo.latest_staus === null) ? 0 : (deviceinfo.latest_staus.port_on_off[index] === false) ? 1 : 0;
                this.listSwitchClickSend(deviceinfo, model, index, status);
            }
        }
    }

    listSwitchClickSend(deviceinfo: any, model: any, index: number, status: number) {
        this.setTimeOut(model, status, index);
        if (deviceinfo.component === DeviceConst.componentrf_powerswitch1
            || deviceinfo.component === DeviceConst.componentrf_powerswitch2
            || deviceinfo.component === DeviceConst.componentrf_powerswitch3) {
            let sendOpType = 1;
            switch (index) {
                case 0:
                    sendOpType = status === 1 ? 1 : 2;
                    break;
                case 1:
                    sendOpType = status === 1 ? 3 : 4;
                    break;
                case 2:
                    sendOpType = status === 1 ? 5 : 6;
                    break;
            }
            if (this.ls.getPlayTTS()) {
                let text = "正在为您" + ((status === 1) ? "打开" : "关闭") + deviceinfo.device_subinfo.items[index] + "中。";
                zhijiaPlugin.ttsPlay(text,"normal");
            }
            //获取当前第一个按钮的状态 0关， 1开,2不控制
            this.eq.Send(deviceinfo.device_id, sendOpType).subscribe(data => {
            });
        } else {//射频开关
            let sendOpType = 1;
            switch (index) {
                case 0:
                    sendOpType = status === 1 ? 1 : 2;
                    break;
                case 1:
                    sendOpType = status === 1 ? 3 : 4;
                    break;
                case 2:
                    sendOpType = status === 1 ? 5 : 6;
                    break;
            }
            if (this.ls.getPlayTTS()) {
                let text = "正在为您" + ((status === 1) ? "打开" : "关闭") + deviceinfo.device_subinfo.items[index] + "中。";
                zhijiaPlugin.ttsPlay(text,"normal");
            }
            //获取当前第一个按钮的状态 0关， 1开,2不控制
            this.eq.Send(deviceinfo.device_id, sendOpType).subscribe(data => {
            });
        }
    }

    switchClickSend(deviceinfo: any, model: any, counterChange: any, index: number, status: number) {
        this.setTimeOut(model, status, index);
        // if (deviceinfo.product_id === DeviceConst.ZWAVE_POWERSWITCH1
        //     || deviceinfo.product_id === DeviceConst.ZWAVE_POWERSWITCH2
        //     || deviceinfo.product_id === DeviceConst.ZWAVE_POWERSWITCH3
        //     || deviceinfo.product_id === DeviceConst.OUTLET1
        //     || deviceinfo.product_id === DeviceConst.OUTLET2
        //     || deviceinfo.product_id === DeviceConst.OUTLET3
        //     || deviceinfo.product_id === DeviceConst.RF_POWERSWITCH1
        //     || deviceinfo.product_id === DeviceConst.RF_POWERSWITCH2
        //     || deviceinfo.product_id === DeviceConst.RF_POWERSWITCH3) {
        if (deviceinfo.component === DeviceConst.componentrf_powerswitch1
            || deviceinfo.component === DeviceConst.componentrf_powerswitch2
            || deviceinfo.component === DeviceConst.componentrf_powerswitch3) {
            let sendOpType = 1;
            switch (index) {
                case 0:
                    sendOpType = status === 1 ? 1 : 2;
                    break;
                case 1:
                    sendOpType = status === 1 ? 3 : 4;
                    break;
                case 2:
                    sendOpType = status === 1 ? 5 : 6;
                    break;
            }
            //获取当前第一个按钮的状态 0关， 1开,2不控制
            this.eq.Send(deviceinfo.device_id, sendOpType).subscribe(data => {
                counterChange.emit({
                    title: this.devname,
                    type: false
                });
            });
        } else {//射频开关

        }
    }

}

export class outlet_status {
    port_on_off: Array<boolean>;

    port_in_use: Array<boolean>;
}


