import { Component, Input, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ExtendedUrlImg } from "../../extended/extendedurlimg";
import { DeviceConst } from "../../interfaces/deviceconst";
import { NavController } from "ionic-angular";
import { DemoproviderProvider } from "../../providers/demoprovider/demoprovider";
import { ChannelProvider } from "../../providers/channel/channel";

/**
 * Generated class for the DemonstrationComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'demonstration',
  templateUrl: 'demonstration.html'
})
export class DemonstrationComponent implements OnInit, OnDestroy {

  deviceinfo: any;//设备信息
  type: string;//显示的模式 0列表 1九宫格 2老人
  online: string;//设备是否在线 
  deviceproduct: string;//设备类型
  src: string;//列表图片路径
  isgroup: boolean = false;//是否是组设备
  subonline: any = null;
  subdeviceState: any = null;
  updateDeviceName: any = null;

  demo_doorbell: string = DeviceConst.DEMO_DOORBELL;
  isatus: boolean = false;

  constructor(private navCtrl: NavController,
    private extimg: ExtendedUrlImg,
    private demopr: DemoproviderProvider,
    private ch: ChannelProvider,
    private ref: ChangeDetectorRef) {

  }

  @Input() set Type(type: string) {
    this.type = type;
  }

  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    this.deviceproduct = this.deviceinfo.product_id;
    this.isgroup = this.deviceinfo.ingroup === 2;
    this.switchDeviceTypeJudgment(0);
  }

  ngOnInit() {
    this.ref.markForCheck();
    if (this.subonline === null)
      this.subonline = this.ch.sub("online").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.switchDeviceTypeJudgment(1, data.data.online);
          this.ref.detectChanges();
        }

      });
  }
  ngOnDestroy() {
    this.ref.detach();
    this.subonline.unsubscribe();
  }

  switchDeviceTypeJudgment(operation: number, data: any = null) {
    switch (this.deviceproduct) {
      case DeviceConst.RF_GATEWAY_DEMO://网关
        switch (operation) {
          case 0:
            if (!this.deviceinfo.onlinestatus)
              this.online = "设备离线";
            else
              this.online = "设备在线";
            this.setImg(DeviceConst.RF_GATEWAY);
            break;
          case 1:
            if (data)
              this.online = "设备在线";
            else
              this.online = "设备离线";
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demogateway');
            break;
        }
        break;

      case DeviceConst.DEMO_DOORBELL://门铃
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DOORBELL);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demodoorbell');
            break;
        }
        break;
      case DeviceConst.DEMO_MENCI://门磁
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DOOR_MAGNET);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demomenci');
            break;
        }
        break;
      case DeviceConst.DEMO_SENSOR://红外感应
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.IRSENSOR);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demosensor');
            break;
        }
        break;
      case DeviceConst.DEMO_GAS://可燃气体
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.GAS_ALARM);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demogas');
            break;
        }
        break;
      case DeviceConst.DEMO_LIGHTS://七彩灯
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DEMO_LIGHTS);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demolights');
            break;
        }
        break;
      case DeviceConst.DEMO_RELAY://模型继电器 开关
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.RF_POWERSWITCH_S13);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('rgbdemolight');
            break;
        }
        break;
      case DeviceConst.DEMO_RGB://DEMO_RGB
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.RF_RGB_01);
            break;
          case 2:
            break;
          case 3:
            switch (this.deviceinfo.device_rgb.type) {
              case 0://GRBW控制器
                this.pushPage('demorgbw');
                break;
              case 3://双色RBG控制器
                this.pushPage('demodoublewy');
                break;
              case 4://单色RBG控制器
                this.pushPage('demosinglew');
                break;
            }
            break;
        }
        break;
      case DeviceConst.DEMO_MANIPULATOR://机械手
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.RF_VALVE);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demomanipulator');
            break;
        }
        break;
      case DeviceConst.DEMO_CURTAIN://电动窗帘
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.CURTAIN);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('democurtain');
            break;
        }
        break;

      case DeviceConst.DEMO_LIGHT://双色灯
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.RF_POWERSWITCH_S13);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demolight');
            break;
        }
        break;
      case DeviceConst.DEMO_LIGHTSDENG://双色灯饮水机
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.CIRCLE_OUTLET);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demodrinking');
            break;
        }
        break;
      case DeviceConst.DEMO_SCREENMP3://串口MP3
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DEMO_SCREENMP3);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demoscreenmp3');
            break;
        }
        break;
      case DeviceConst.DEMO_NEWWIND://新风
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DEMO_NEWWIND);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demonewwind');
            break;
        }
        break;
      case DeviceConst.DEMO_AIR://空调
        switch (operation) {
          case 0:
            this.setImg(DeviceConst.DEMO_AIR);
            break;
          case 2:
            break;
          case 3:
            this.pushPage('demoair');
            break;
        }
        break;
    }
  }
  setImg(name: string) {
    this.src = this.extimg.getlist_64(name);
  }
  pushPage(pageName: string) {
    this.navCtrl.push(pageName, { device_id: this.deviceinfo.device_id });
  }
  //转到详细页面
  pushDPage() {
    this.switchDeviceTypeJudgment(3);
  }
  //安门铃事件
  doorbellclick() {
    this.isatus = true;
    this.demopr.Send(this.deviceinfo.device_id, 0x21, "1").subscribe(data => {
    });
    setTimeout(() => {
      this.isatus = false;
    }, 2500);
  }
}



