import { Component, Input, OnInit, OnDestroy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { DeviceConst } from '../../interfaces/deviceconst';
import { ExtendedUrlImg } from '../../extended/extendedurlimg';
import { ExtendedOutlet } from './extendedoutlet';
import { ChannelProvider } from '../../providers/channel/channel';
import { EquipmentProvider } from "../../providers/equipment/equipment";
import { NavController } from "ionic-angular";
import { LocaldataProvider } from '../../providers/localdata/localdata';
import { ColorPickerService } from '../colorpicker/lib/color-picker.service';
import { elementDef } from '@angular/core/src/view/element';

/**
 * RF一,二,三开关
 * 小圆插
 * RF幕布开关
 * 幕布开关（零火）
 * 窗帘开关（零火）
 * 门铃
 * 电动幕布
 * 红外感应器
 * 电视遥控器
 * 空调遥控器
 * 机顶盒控器
 * DVD控器
 * 智能加湿器智能家电
 * 风扇智能家电
 * 7寸背景音乐智能主机智能音乐
 * 3寸背景音乐智能主机智能音乐
 * 智能电动婴儿床智能家电
 * 智能宠物喂食器智能家电
 * 智能空气净化器智能家电
 * 功放智能家电
 * 射频开关
 * 烟雾报警器
 * 漏水报警器
 * RGB控制器
 * 阀门[智能燃气机械手,zwave智能阀门]
 * 
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'switch-device',
  templateUrl: 'switch.html',
  providers: [ColorPickerService]
})
export class SwitchComponent implements OnInit, OnDestroy {

  deviceinfo: any;//设备信息
  type: string;//显示的模式 0列表 1九宫格 2老人
  online: string;//设备是否在线 
  devicetype: string;//要显示的设备类型 对应html
  deviceproduct: string;//设备类型
  src: string;//列表图片路径
  isgroup: boolean = false;//是否是组设备
  subonline: any = null;
  subdeviceState: any = null;
  updateDeviceName: any = null;
  /******一,二,三开关******* */
  switch1: string = "switch1";
  switch2: string = "switch2";
  switch3: string = "switch3";
  buttonstatus1: Object = "in-close";
  buttonstatus2: Object = "in-close";
  buttonstatus3: Object = "in-close";
  name1: string;
  name2: string;
  name3: string;
  model: OutletModel = new OutletModel();

  /**
   * 
   * @param extimg rf 小圆插
   * @param extended 
   * @param ch 
   */
  rfoutlet: string = "rfoutlet";
  checked: boolean = false;//新值
  oldchecked: boolean = false;//原始值
  stattype: boolean = false;
  /**
   * 门铃
   * @param navCtrl 
   * @param extimg 
   * @param extended 
   * @param ch 
   * @param eq 
   */
  rfdoorbell: string = "rfdoorbell";
  isatus: boolean = false;
  /**
   * 阀门[智能燃气机械手,zwave智能阀门]
   * @param navCtrl 
   * @param extimg 
   * @param extended 
   * @param ch 
   * @param eq 
   */
  fmvalue: string = "fmvalue";

  close: number = 0;
  w: number = 0;
  y: number = 176;
  ct: number = 50;//色温
  bn: number = 50;//亮度 
  on_off: boolean = true;
  grbon_off: boolean = true;

  @ViewChild('lightswitchcontent') lightswitchcontentElement: any;
  constructor(private navCtrl: NavController,
    private extimg: ExtendedUrlImg,
    private extended: ExtendedOutlet,
    private ch: ChannelProvider,
    private eq: EquipmentProvider,
    private ref: ChangeDetectorRef,
    private ls: LocaldataProvider
  ) {

  }

  ionViewDidLoad() { }

  ionViewWillUnload() { }
  ngOnInit() {
    this.ref.markForCheck();
    if (this.subonline === null)
      this.subonline = this.ch.sub("offline").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.switchDeviceTypeJudgment(1, data);
          this.ref.detectChanges();
        }
      });
    if (this.subdeviceState === null)
      this.subdeviceState = this.ch.sub("deviceState").subscribe(data => {
        //console.log(data);
        if (data.data.device_id === this.deviceinfo.device_id) {
          if (this.deviceinfo.ingroup === 1) return;
          this.switchDeviceTypeJudgment(2, data);
          this.ref.detectChanges();
        }
      });
    if (this.updateDeviceName === null)
      this.updateDeviceName = this.ch.sub("updateDeviceName").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.deviceinfo.name = data.data.name;
          this.ref.detectChanges();
        }
      });
  }
  ngOnDestroy() {
    this.ref.detach();
    this.subonline.unsubscribe();
    this.subdeviceState.unsubscribe();
    this.updateDeviceName.unsubscribe();
  }
  @Input() set Type(type: string) {
    this.type = type;
  }

  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    this.deviceproduct = this.deviceinfo.product_id;
    this.isgroup = this.deviceinfo.ingroup === 2;
    this.switchDeviceTypeJudgment(0);
    this.src = this.extimg.getlist_64(this.deviceinfo.product_id);
  }

  click1() {
    //获取当前第一个按钮的状态 0关， 1开,2不控制
    this.extended.listSwitchClick(this.deviceinfo, this.model, 0);
  }
  click2() {
    this.extended.listSwitchClick(this.deviceinfo, this.model, 1);
  }
  click3() {
    this.extended.listSwitchClick(this.deviceinfo, this.model, 2);
  }

  //小圆插开关
  clickvalue() {
    if (this.stattype) {
      this.checked = this.checked ? false : true;
      return
    };
    this.stattype = true;
    let on_off = this.deviceinfo.latest_staus.port_on_off[0] ? 0 : 1;
    this.eq.Send(this.deviceinfo.device_id, on_off).subscribe(data => {
      setTimeout(() => {
        if (this.stattype) {
          this.stattype = false;
          this.checked = this.oldchecked;
        }
      }, 2000);
    });;
  }

  doorbellclick() {
    this.isatus = true;
    this.eq.Send(this.deviceinfo.device_id, 0).subscribe(data => {
    });
    setTimeout(() => {
      this.isatus = false;
    }, 2500);
  }
  //阀门
  fmclickvalue() {
    if (this.stattype) {
      this.checked = this.checked ? false : true;
      return
    };
    this.stattype = true;
    if (this.deviceinfo.latest_staus != null) {
      let on_off = this.deviceinfo.latest_staus.on_off ? 0 : 1;
      this.eq.Send(this.deviceinfo.device_id, on_off).subscribe(data => {
        setTimeout(() => {
          if (this.stattype) {
            this.stattype = false;
            this.checked = this.oldchecked;
          }
        }, 2000);
      });;
    }
  }
  //RGB
  intervalValRgb: any;
  r: number = 0;
  g: number = 0;
  b: number = 0;
  lastrgb: number[] = [0, 0, 0, 0];
  changeColor() {
    clearInterval(this.intervalValRgb);
    this.startFreshRGB();
  }
  startFreshRGB() {
    this.intervalValRgb = setInterval(data => {
      console.log('check');
      if (this.lastrgb[0] == this.r && this.lastrgb[1] == this.g && this.lastrgb[2] == this.b) {
        console.log('send');
        this.changeLightColorRGB();
        clearInterval(this.intervalValRgb);
      } else {
        this.lastrgb[0] = this.r;
        this.lastrgb[1] = this.g;
        this.lastrgb[2] = this.b;
        this.ref.detectChanges();
      }
    }, 300);
  }
  changeLightColorRGB() {
    this.eq.Send(this.deviceinfo.device_id, 9999, { lastrgbw: this.lastrgb.join(','), time: 1 }).subscribe(success => {
      console.log('指令发送成功');
      this.ref.detectChanges();
    }, error => {

    });
  }
  // clickldrgb() {
  //   if (this.grbon_off) {
  //     this.eq.Send(this.deviceinfo.device_id, 2, { lastrgbw: this.lastrgbw.join(','), time: this.deviceinfo.device_rgb.time }).subscribe(success => {
  //       this.on_off = false;
  //       this.ref.detectChanges();
  //     }, error => {

  //     });
  //   } else {
  //     this.changeLightColorRGB();
  //     this.on_off = true;
  //     this.ref.detectChanges();
  //   }
  // }

  clickld() {
    clearInterval(this.intervalVal);
    this.intervalVal = setInterval(data => {
      this.bn = this.close;
      this.lastrgbw[0] = 255 * (100 - this.ct) / 100 * this.close / 100;
      this.lastrgbw[1] = 255 * this.ct / 100 * this.close / 100;
      console.log("值一:" + this.lastrgbw[0]);
      console.log("值二:" + this.lastrgbw[1]);
      this.lastrgbw[2] = this.ct;
      this.lastrgbw[3] = this.bn;
      if (this.lastrgbw[0] != this.w || this.lastrgbw[1] != this.y || this.lastrgbw[2] != this.ct || this.lastrgbw[3] != this.bn) {
      
        this.changeLightColor();
      } else {
        clearInterval(this.intervalVal);
      }
    }, 300);






    //w = 255 * (100 - ct) / 100 * bn / 100;
    // this.eq.Send(this.deviceinfo.device_id, 9999, { lastrgbw: this.lastrgbw.join(','), time: this.deviceinfo.device_rgb.time }).subscribe(success => {
    //   this.ref.detectChanges();
    // }, error => {
    // });
  }

  //RGB END
  //转到详细页面
  pushPage() {
    this.switchDeviceTypeJudgment(3);
  }

  switchDeviceTypeJudgment(operation: number, data: any = null) {
    switch (this.deviceproduct) {
      case DeviceConst.ZWAVE_VALVE://阀门
      case DeviceConst.RF_VALVE://智能燃气机械手
        {
          switch (operation) {
            case 0:
              this.devicetype = "fmvalue";
              if (this.deviceinfo.latest_staus != null) {
                this.checked = this.deviceinfo.latest_staus.on_off ? true : false;
                this.oldchecked = this.deviceinfo.latest_staus.on_off ? true : false;
              }
              break;
            case 2:
              this.fmoutlestat(data);
              break;
            case 3:
              this.navCtrl.push('zwavevalve', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }

      case DeviceConst.WATER_SENSOR://漏水报警器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('watersensor', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_WY_01://WY控制器
      case DeviceConst.RF_WY_02://WY控制器
      case DeviceConst.RF_WY_03://WY控制器
      case DeviceConst.RF_WY_04://WY控制器
      //   {
      //     switch (operation) {
      //       case 3:
      //         this.navCtrl.push('manage_rf_wy', { 'device_id': this.deviceinfo.device_id });
      //         break;
      //     }
      //     break;
      //   }
      // case DeviceConst.RF_RGB_01://RGB控制器
      //   {
      //     switch (operation) {
      //       case 3:
      //         this.navCtrl.push('manage_rf_rgb', { 'device_id': this.deviceinfo.device_id });
      //         break;
      //     }
      //     break;
      //   }
      // case DeviceConst.ZWAVE_LIGHTSWITH:
      //   switch (operation) {
      //     case 3:
      //       this.navCtrl.push('lightswith', { 'device_id': this.deviceinfo.device_id });
      //       break;
      //   }
      //   break;
      case DeviceConst.RF_RADAR_LED_01://RF雷达冷暖LED控制器
        {
          switch (operation) {
            case 0:
              this.devicetype = "rf_radar_led_01";
              if (this.deviceinfo.latest_staus != null) {
                this.on_off = this.deviceinfo.latest_staus.on_off;//true 开的状态 false 关的状态
                // if (this.on_off)
                //     this.openstyle = "button-power button-activated button button-md button-outline button-outline-md button-outline-md-default";
                // else
                //     this.openstyle = "button-power button button-md button-outline button-outline-md button-outline-md-default";
              }
              var dls = this.deviceinfo.device_rgb;
              if (dls.w == null || isNaN(dls.w)) {
                dls.w = 0;
              }
              if (dls.y == null || isNaN(dls.y)) {
                dls.y = 0;
              }
              if (dls.ct == null || isNaN(dls.ct)) {
                dls.ct = 0;
              }
              if (dls.bn == null || isNaN(dls.bn)) {
                dls.bn = 0;
              }
              this.deviceinfo.device_rgb = dls;
              this.w = dls.w;
              this.y = dls.y;
              this.ct = dls.ct;
              this.bn = dls.bn;
              this.close = dls.bn;//调亮度用
              this.ref.detectChanges();
              break;
            case 2:
              if (data.data.data === undefined)
                return;
              this.deviceinfo.latest_staus = data.data.data;
              {
                if (this.deviceinfo.latest_staus != null) {
                  this.on_off = this.deviceinfo.latest_staus.on_off;//true 开的状态 false 关的状态
                  // if (this.on_off)
                  //     this.openstyle = "button-power button-activated button button-md button-outline button-outline-md button-outline-md-default";
                  // else
                  //     this.openstyle = "button-power button button-md button-outline button-outline-md button-outline-md-default";
                }
                let dls = this.deviceinfo.device_rgb;
                if (dls.w == null || isNaN(dls.w)) {
                  dls.w = 0;
                }
                if (dls.y == null || isNaN(dls.y)) {
                  dls.y = 0;
                }
                if (dls.ct == null || isNaN(dls.ct)) {
                  dls.ct = 0;
                }
                if (dls.bn == null || isNaN(dls.bn)) {
                  dls.bn = 0;
                }
                this.deviceinfo.device_rgb = dls;
                this.w = dls.w;
                this.y = dls.y;
                this.ct = dls.ct;
                this.bn = dls.bn;
              }
              break;
            case 3:
              if (this.deviceinfo.latest_staus !== null && this.deviceinfo.latest_staus.radar_on_off)
                this.navCtrl.push("rfradar", { device_id: this.deviceinfo.device_id });
              else
                this.navCtrl.push('manage_rf_radar_led', { 'device_id': this.deviceinfo.device_id });//要判断一下跳那个页面 暂时默认
              break;
          }
          break;
        }
      case DeviceConst.GAS_ALRAM_DEMO:
      case DeviceConst.SMOKE_ALARM://烟雾报警器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('smokealarm', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_SWITCHES://射频开关
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('rfswitches', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.Amplifier01://功放智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('sodcast', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.AIR_PURIFIER://智能空气净化器智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('aircleaner', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.IR_PET_FEEDER://智能宠物喂食器智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('autozdwsq', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.BABY_BED://智能电动婴儿床智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('babybed', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.WFMUSIC_01://7寸背景音乐智能主机智能音乐
      case DeviceConst.BTMUSIC_01://3寸背景音乐智能主机智能音乐
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('musicplayer', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_AIRFAN://风扇智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('airfan', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_HUMIDIFIER://智能加湿器智能家电
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('humidifier', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_DVD://DVD控器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('manage_dvd', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_TVBOX://机顶盒控器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('manage_tvbox', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_ARC://空调遥控器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('manage_arc', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.VT_TV://电视遥控器
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('manage_tv', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.IRSENSOR_DEMO:
      case DeviceConst.IRSENSOR:
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('irsensor', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.GAS_ALARM:
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('gasalarm', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.ELETRICCURTAIN:
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('electriccurtain', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.DOORBELL:
        {
          switch (operation) {
            case 0:
              this.devicetype = "rfdoorbell";
              break;
            case 3:
              this.navCtrl.push('doorbell', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_POWERSWITCH_N33://幕布开关（零火）
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('mbkg', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_POWERSWITCH_N23://窗帘开关（零火）
        {
          switch (operation) {
            case 3:
              this.navCtrl.push('curtainswitch', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.CIRCLE_OUTLET:
        {
          switch (operation) {
            case 0:
              this.devicetype = "rfoutlet";
              if (this.deviceinfo.latest_staus != null) {
                this.checked = this.deviceinfo.latest_staus.port_on_off[0];
                this.oldchecked = this.deviceinfo.latest_staus.port_on_off[0];
              }
              break;
            case 1:
              this.offlinestat(data);
              break;
            case 2:
              this.rfoutlestat(data);
              break;
            case 3:
              this.navCtrl.push('circleoutle', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_POWERSWITCH_S11:
      case DeviceConst.RF_POWERSWITCH_S31:
      case DeviceConst.RF_POWERSWITCH_S41:
      case DeviceConst.RF_POWERSWITCH_N11:
      case DeviceConst.ZWAVE_POWERSWITCH_S11:
      case DeviceConst.ZWAVE_POWERSWITCH_N11:
        {
          switch (operation) {
            case 0:
              this.devicetype = "switch1";
              this.extended.refresh(this.deviceinfo, this.model);
              this.ref.detectChanges();
              break;
            case 1:
              this.offlinestat(data);
              break;
            case 2:
              this.switchreturnstat(data);
              break;
            case 3:
              this.navCtrl.push('switch', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_POWERSWITCH_S12:
      case DeviceConst.RF_POWERSWITCH_S32:
      case DeviceConst.RF_POWERSWITCH_S42:
      case DeviceConst.RF_POWERSWITCH_N12:
      case DeviceConst.ZWAVE_POWERSWITCH_S12:
      case DeviceConst.ZWAVE_POWERSWITCH_N12:
        {
          switch (operation) {
            case 0:
              this.devicetype = "switch2";
              this.extended.refresh(this.deviceinfo, this.model);
              this.ref.detectChanges();
              break;
            case 1:
              this.offlinestat(data);
              break;
            case 2:
              this.switchreturnstat(data);
              this.stattype = false;
              this.deviceinfo.latest_staus.on_off = data.data.services.on_off;
              this.checked = this.deviceinfo.latest_staus.on_off ? true : false;
              this.oldchecked = this.deviceinfo.latest_staus.on_off ? true : false;
              break;
            case 3:
              this.navCtrl.push('switch', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_POWERSWITCH_S13:
      case DeviceConst.RF_POWERSWITCH_S33:
      case DeviceConst.RF_POWERSWITCH_S43:
      case DeviceConst.RF_POWERSWITCH_N13:
      case DeviceConst.ZWAVE_POWERSWITCH_S13:
      case DeviceConst.ZWAVE_POWERSWITCH_N13:
        {
          switch (operation) {
            case 0:
              this.devicetype = "switch3";
              this.extended.refresh(this.deviceinfo, this.model);
              this.ref.detectChanges();
              break;
            case 1:
              this.offlinestat(data);
              break;
            case 2:
              this.switchreturnstat(data);
              break;
            case 3:
              this.navCtrl.push('switch', { 'device_id': this.deviceinfo.device_id });
              break;
          }
          break;
        }
      case DeviceConst.RF_RGB_01:
        {
          switch (operation) {
            case 0:
              this.devicetype = "rgbdevice";
              if (this.deviceinfo.latest_staus != null) {
                this.grbon_off = this.deviceinfo.latest_staus.on_off;
                // if (this.grbon_off)
                //   this.close = 255;
                // else
                //   this.close = 1;
              }
              let rgbValue = this.deviceinfo.device_rgb;
              this.lastrgb[0] = rgbValue.r;
              this.lastrgb[1] = rgbValue.g;
              this.lastrgb[2] = rgbValue.b;
              this.r = rgbValue.r;
              this.g = rgbValue.g;
              this.b = rgbValue.b;
              this.ref.detectChanges();
              break;
            case 1:
              break;
            case 2:
              break;
            case 3:
              break;
          }
        }
    }
  }
  offlinestat(data: any) {
    if (!data.data.services.offline) { this.online = "设备离线"; }
    this.ref.detectChanges();
  }
  switchreturnstat(data: any) {
    if (this.deviceinfo.latest_staus === null)
      this.deviceinfo.latest_staus = outlet_status;
    this.deviceinfo.latest_staus.port_on_off = data.data.services.port_on_off;
    this.extended.refresh(this.deviceinfo, this.model);
    this.online = "";
    this.ref.detectChanges();
  }

  rfoutlestat(data: any) {
    this.stattype = false;
    if (this.deviceinfo.latest_staus === null)
      this.deviceinfo.latest_staus = outlet_status;
    this.deviceinfo.latest_staus.port_on_off = data.data.services.port_on_off;
    this.checked = this.deviceinfo.latest_staus.port_on_off[0];
    this.oldchecked = this.deviceinfo.latest_staus.port_on_off[0];
    this.online = "";
    this.ref.detectChanges();
  }
  fmoutlestat(data: any) {
    this.stattype = false;
    this.deviceinfo.latest_staus.on_off = data.data.data.data.on_off;
    this.checked = this.deviceinfo.latest_staus.on_off ? true : false;
    this.oldchecked = this.deviceinfo.latest_staus.on_off ? true : false;
    this.ref.detectChanges();
  }

  showHeaderAndFooter: boolean = true;
  intervalVal: any;
  lastrgbw: number[] = [0, 0, 0, 0];
  contentHeight: number = 40;
  colorAreaHeight: number = 40;
  public ngAfterViewInit() {
    // let element = this.lightswitchcontentElement;
    // console.log(element);
    // let height = parseInt(element._elementRef.nativeElement.offsetHeight);
    // console.log(height);
    // this.colorAreaHeight = height;
    // this.contentHeight = height;
    // this.ref.detectChanges();
  }
  touchstate(state: boolean) {
    this.showHeaderAndFooter = !state;
    if (!state) {
      clearInterval(this.intervalVal);
      this.startFresh();
    }
    this.ref.detectChanges();
  }
  onChangeTemperature(val: { bn: number, ct: number }) {
    this.ct = val.ct;
    this.bn = val.bn <= 0 ? 50 : val.bn;
    this.y = 255 * this.ct / 100 * this.bn / 100;
    this.w = 255 * (100 - this.ct) / 100 * this.bn / 100;
    console.log(val);
    this.ref.detectChanges();
    // clearInterval(this.intervalVal);
    // this.startFresh();
  }
  //開始刷新
  startFresh() {
    this.intervalVal = setInterval(data => {
      console.log('check');
      if (this.lastrgbw[0] != this.w || this.lastrgbw[1] != this.y || this.lastrgbw[2] != this.ct || this.lastrgbw[3] != this.bn) {
        console.log('send');
        this.lastrgbw[0] = this.w;
        this.lastrgbw[1] = this.y;
        this.lastrgbw[2] = this.ct;
        this.lastrgbw[3] = this.bn;
        this.changeLightColor();
      } else {
        clearInterval(this.intervalVal);
      }
    }, 100);
  }
  changeLightColor() {
    this.deviceinfo.device_rgb.w = this.lastrgbw[0];
    this.deviceinfo.device_rgb.y = this.lastrgbw[1];
    this.deviceinfo.device_rgb.ct = this.lastrgbw[2];
    this.deviceinfo.device_rgb.bn = this.lastrgbw[3];
    this.ls.updateLocalDevice(this.deviceinfo);
    this.eq.Send(this.deviceinfo.device_id, 9999, { lastrgbw: this.lastrgbw.join(','), time: this.deviceinfo.device_rgb.time }).subscribe(success => {
      console.log('指令发送成功');
      this.on_off = true;
      clearInterval(this.intervalVal);
      // this.close =100;
    }, error => {

      clearInterval(this.intervalVal);
    });
  }
  // clickld() {
  //   console.log(this.on_off);
  //   if (this.on_off) {
  //     this.eq.Send(this.deviceinfo.device_id, 2, { lastrgbw: this.lastrgbw.join(','), time: this.deviceinfo.device_rgb.time }).subscribe(success => {
  //       console.log('指令发送成功');
  //       this.on_off = false;
  //     }, error => {

  //     });
  //   } else {
  //     this.lastrgbw[0] = this.w;
  //     this.lastrgbw[1] = this.y;
  //     this.lastrgbw[2] = this.ct;
  //     this.lastrgbw[3] = this.bn;
  //     this.eq.Send(this.deviceinfo.device_id, 1, { lastrgbw: this.lastrgbw.join(','), time: this.deviceinfo.device_rgb.time }).subscribe(success => {
  //       console.log('指令发送成功');
  //       this.on_off = true;
  //     }, error => {

  //     });
  //     // this.on_off = true;
  //   }

  // }
}


export class OutletModel {

  buttonstatus1: string = "in-close";
  buttonstatus2: string = "in-close";
  buttonstatus3: string = "in-close";

  name1: string = "开关";
  name2: string = "开关";
  name3: string = "开关";
}

export class outlet_status {
  port_on_off: Array<boolean>;

  port_in_use: Array<boolean>;
}


