import { Component, Input } from '@angular/core';

@Component({
    selector: "gradient-component",
    templateUrl: "./gradient.component.html",
})
//开关按钮样式状态
export class GradientComponent {
    type: string;
    value: string;
    @Input() set mytype(mytype: string) {
        this.type = mytype;
    }

    @Input() set myvalue(myvalue: string) {
        this.value = myvalue;
    }
}


