import { Component, Input } from '@angular/core';
import { DeviceConst } from '../../interfaces/deviceconst';

/**
 * Generated class for the ShebeiComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'shebei-info',
  templateUrl: 'shebei.html'
})
export class ShebeiComponent {
  device: any;
  type: string = "";
  bindshebei: boolean = true;
  componenttype: string;
  isgroup: boolean = false;//是否是组设备
  constructor() { }

  @Input() set myroundstatus(equipment: any) {
    this.device = equipment;
    this.isgroup = this.device.ingroup === 2;
    if (this.device.main_device_id == null || this.device.main_device_id == "")//判断是否子设备
    {
      this.bindshebei = true;
      switch (this.device.product_id) {
        case DeviceConst.WF_CIRCLE_OUTLET:
        case DeviceConst.RF_GATEWAY:
        case DeviceConst.CURTAIN:
        case DeviceConst.Airradio_A1://空气电台
        case DeviceConst.Airradio_A2:
        case DeviceConst.Airradio_A2se:
        case DeviceConst.IR_GATEWAY:
        // case DeviceConst.TC1_CAMERA:
        // case DeviceConst.TP1_CAMERA:
        case DeviceConst.ZWAVE_GATEWAY:
          this.componenttype = "wifidevice";
          break;
        case DeviceConst.RF_POWERSWITCH_N11:
        case DeviceConst.RF_POWERSWITCH_N12:
        case DeviceConst.RF_POWERSWITCH_N13:
        case DeviceConst.RF_POWERSWITCH_N23:
        case DeviceConst.RF_POWERSWITCH_N33:
        case DeviceConst.RF_POWERSWITCH_S11:
        case DeviceConst.RF_POWERSWITCH_S12:
        case DeviceConst.RF_POWERSWITCH_S13:
        case DeviceConst.RF_POWERSWITCH_S31:
        case DeviceConst.RF_POWERSWITCH_S32:
        case DeviceConst.RF_POWERSWITCH_S33:
        case DeviceConst.RF_POWERSWITCH_S41:
        case DeviceConst.RF_POWERSWITCH_S42:
        case DeviceConst.RF_POWERSWITCH_S43:
        case DeviceConst.ZWAVE_POWERSWITCH_N11:
        case DeviceConst.ZWAVE_POWERSWITCH_N12:
        case DeviceConst.ZWAVE_POWERSWITCH_N13:
        case DeviceConst.ZWAVE_POWERSWITCH_S11:
        case DeviceConst.ZWAVE_POWERSWITCH_S12:
        case DeviceConst.ZWAVE_POWERSWITCH_S13:
        case DeviceConst.CIRCLE_OUTLET:
        case DeviceConst.DOORBELL:
        case DeviceConst.ELETRICCURTAIN:
        case DeviceConst.GAS_ALARM:
        case DeviceConst.IRSENSOR:
        case DeviceConst.VT_TV:
        case DeviceConst.VT_ARC:
        case DeviceConst.VT_TVBOX:
        case DeviceConst.VT_DVD:
        case DeviceConst.VT_HUMIDIFIER:
        case DeviceConst.VT_AIRFAN:
        case DeviceConst.BTMUSIC_01:
        case DeviceConst.WFMUSIC_01:
        case DeviceConst.BABY_BED:
        case DeviceConst.IR_PET_FEEDER:
        case DeviceConst.AIR_PURIFIER:
        case DeviceConst.Amplifier01:
        case DeviceConst.RF_SWITCHES:
        case DeviceConst.SMOKE_ALARM:
        case DeviceConst.WATER_SENSOR:
        case DeviceConst.RF_RGB_01:
        case DeviceConst.RF_WY_04:
        case DeviceConst.RF_WY_03:
        case DeviceConst.RF_WY_02:
        case DeviceConst.RF_WY_01:
        case DeviceConst.RF_VALVE:
        case DeviceConst.ZWAVE_VALVE:
        case DeviceConst.IRSENSOR_DEMO:
        case DeviceConst.GAS_ALRAM_DEMO:
        case DeviceConst.RF_RADAR_LED_01:
          this.componenttype = "switchdevice";
          break;
        case DeviceConst.DOOR:
        case DeviceConst.DOOR_MAGNET:
          this.componenttype = "doorlock"
          break;
        case DeviceConst.ZWAVE_LIGHTSWITH:
          this.componenttype = "zwavedevice";
          break;
        case DeviceConst.RF_GATEWAY_DEMO:
        case DeviceConst.DEMO_DOORBELL:
        case DeviceConst.DEMO_MENCI:
        case DeviceConst.DEMO_SENSOR:
        case DeviceConst.DEMO_GAS:
        case DeviceConst.DEMO_LIGHTS:
        case DeviceConst.DEMO_CURTAIN:
        case DeviceConst.DEMO_LIGHT:
        case DeviceConst.DEMO_SCREENMP3:
        case DeviceConst.DEMO_NEWWIND:
        case DeviceConst.DEMO_MANIPULATOR:
        case DeviceConst.DEMO_AIR:
        case DeviceConst.DEMO_LIGHTSDENG:
        case DeviceConst.DEMO_RELAY:
        case DeviceConst.DEMO_RGB:
          this.componenttype = "demodevice";
          break;
        case DeviceConst.DEMO_AIR_1:
          this.componenttype = "demoair1"
          break;
        case DeviceConst.TC1_CAMERA:
        case DeviceConst.TP1_CAMERA:
          this.componenttype = "democamera"
          break;  
      }
    } else {
      this.bindshebei = false;
    }
  }

  @Input() set Type(type: string) {
    this.type = type;
  }
}



