import { Component, Output, EventEmitter, OnInit, OnDestroy, Input, ChangeDetectorRef } from '@angular/core';
import { OutletModel, outlet_status } from './switch';
import { ChannelProvider } from "../../providers/channel/channel";
import { ExtendedOutlet } from "./extendedoutlet";
import { DeviceConst } from '../../interfaces/deviceconst';
/**
 * Generated class for the SwitchdetailedaComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'switchdetailed',
  templateUrl: 'switchdetaileda.html'
})
export class SwitchdetailedaComponent implements OnInit, OnDestroy {

  //设备信息
  deviceinfo: any;
  devname: string = "";
  model: OutletModel = new OutletModel();
  deviceState: any = null;
  updateDeviceGroup: any = null;
  subonline: any = null;
  swittype: string;
  swittypea: string = "powerswitch1";
  swittypeb: string = "powerswitch2";
  swittypec: string = "powerswitch3";
  index: number = 0;
  constructor(
    private ch: ChannelProvider,
    private extended: ExtendedOutlet,
    private ref: ChangeDetectorRef
  ) {

  }

  //当前开关的所有属性
  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    if (this.deviceinfo === null) return;
    switch (this.deviceinfo.component) {
      case DeviceConst.componentrf_powerswitch1:
        this.swittype = "powerswitch1";
        break;
      case DeviceConst.componentrf_powerswitch2:
        this.swittype = "powerswitch2";
        break;
      case DeviceConst.componentrf_powerswitch3:
        this.swittype = "powerswitch3";
        break;
    }
    this.extended.refresh(this.deviceinfo, this.model);

  }
  ngOnInit() {
    this.ref.markForCheck();
    if (this.subonline === null)
      this.subonline = this.ch.sub("offline").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          console.log('设备离线offline');
          this.counterChange.emit({
            title: "设备离线",
            type: true
          });
          this.ref.detectChanges();
        }
      });
    if (this.deviceState === null) {
      this.deviceState = this.ch.sub("deviceState").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          if (this.deviceinfo.latest_staus === null)
            this.deviceinfo.latest_staus = outlet_status;
          this.deviceinfo.latest_staus.port_on_off = data.data.services.port_on_off;
          this.deviceinfo.latest_staus.port_in_use = data.data.services.port_in_use;
          this.extended.refresh(this.deviceinfo, this.model);
          let returnDevname = this.extended.getOutletName(this.deviceinfo, this.index, true);
          this.counterChange.emit({
            title: returnDevname,
            type: true
          });
          this.ref.detectChanges();
        }
      });
    }
    if (this.updateDeviceGroup === null) {
      this.updateDeviceGroup = this.ch.sub("updateDeviceGroup").subscribe(data => {
        if (data.data.device_id === this.deviceinfo.device_id) {
          this.deviceinfo.group_devices = data.data.group_devices;
          this.deviceinfo.device_subinfo = data.data.device_subinfo;
          this.extended.refresh(this.deviceinfo, this.model);
        }
        this.ref.detectChanges();
      });
    }
  }

  ngOnDestroy() {
    this.ref.detach();
    this.subonline.unsubscribe();
    this.deviceState.unsubscribe();
    this.updateDeviceGroup.unsubscribe();
  }
  @Output('change') counterChange = new EventEmitter();

  click1() {
    this.index = 0
    this.extended.switchClick(this.deviceinfo, this.model, this.counterChange, this.index);
  }
  click2() {
    this.index = 1
    this.extended.switchClick(this.deviceinfo, this.model, this.counterChange, this.index);
  }
  click3() {
    this.index = 2
    this.extended.switchClick(this.deviceinfo, this.model, this.counterChange, this.index);
  }
}




