import { NgModule } from '@angular/core';
import { IonicModule } from 'ionic-angular';
import { ShebeiComponent } from './shebei';
import { WifideviceComponentModule } from "../wifidevice/wifidevice.module";
import { SwitchComponentModule } from "../switch/switch.module";
import { DoorlockComponentModule } from "../doorlock/doorlock.module";
import { DemonstrationComponentModule } from "../demonstration/demonstration.module";
import { AirdetectorComponentModule } from "../airdetector/airdetector.module";
import { DemoncameraComponentModule } from "../demoncamera/demoncamera.module";
@NgModule({
  declarations: [
    ShebeiComponent
  ],
  imports: [
    IonicModule,
    WifideviceComponentModule,
    SwitchComponentModule,
    DoorlockComponentModule,
    DemonstrationComponentModule,
    AirdetectorComponentModule,
    DemoncameraComponentModule
  ],
  exports: [
    ShebeiComponent
  ]
})
export class ShebeiComponentModule { }



