  //检查是否为二维码添加设备
  dealS() {
    var s = this.ls.getS();
    var state = this.ls.getSstate();
    if (s != 0) {
      if (state == "0") {
        //未处理新增
        switch (s) {
          case 1:
            // this.navCtrl.push(BindGatewayPage, { product_id: DeviceConst.RF_GATEWAY, productname: "mini智能主机", roomid: 0 });
            break;
        }
        this.ls.setS(0);
        this.ls.setSstate('');
      }
    }
  }

