  //页面加载时运行。此事件仅在每个页面创建时发生一次。如果页面离开但已被缓存，则此事件将不会在后续查看时再次触发。该ionViewDidLoad事件是放置页面的设置代码的好地方。
  ionViewDidLoad() {

    this.setvideoTimeOut();

    this.PlaySceneState = this.ls.getPlayScene();
    this.PlayTTSState = this.ls.getPlayTTS();

    Emitter.register("musicState", this.UpdateMusicState, this);//homne上的按键响应

    this.ref.markForCheck();
    Emitter.register("updatefamily", this.GetUserBasicInformation, this);//切换家庭事件注册
    this.updateDeviceList = this.channelService.sub("updateDeviceList").subscribe(data => {
      this.GetUserBasicInformation();//加载基本数据
    });
    this.updateScene = this.channelService.sub("updateScene").subscribe(data => {
      this.getSceneList();//加载基本数据
      this.ref.detectChanges();
    });

    //获取实时天所
    this.ext.FindWeather().subscribe(result => {
      if (result.error_code === 0) {
        this.ls.setWeather(result.data);
        this.weather = result.data;
        let weatname = this.weather.realtime.weather;
        this.weatherico = this.ext.WeatherIonic(weatname);
      }

    }, error => {
    });

    let elements = document.querySelectorAll(".tabbar");
    if (elements != null) {
      Object.keys(elements).map((key) => {
        elements[key].style.display = 'none';
      });
    }

    // var AppVersion = cordova.require('cordova-plugin-appversion.app-version');
    // var version = AppVersion.build;
    // console.log(version);

  }

  private UpdateMusicState(eventName: any, state: string, volume: number) {
    switch (state) {
      case "stoped":
        this.PlayStyle = "bofang";
        break;
      case "play":
        this.PlayStyle = "zanting";
        break;
      case "pause":
        this.PlayStyle = "bofang";
        break;
      case "sysvolume_minus":
        this.getSysVolume();
        break;
      case "sysvolume_plus":
        this.getSysVolume();
        break;
      case "volume_plus":
        this.playVolume += 10;
        //cordova.plugins.VolumeControl.setVolume(this.playVolume / 100);
        break;
      case "volume_minus":
        this.playVolume -= 10;
        //cordova.plugins.VolumeControl.setVolume(this.playVolume / 100);
        break;
      case "volume_max":
        this.playVolume = 100;
        //cordova.plugins.VolumeControl.setVolume(1);
        break;
      case "volume_min":
        this.playVolume = 0;
        //cordova.plugins.VolumeControl.setVolume(0);
        break;
      case "volume_select":
        this.playVolume = volume;
        //cordova.plugins.VolumeControl.setVolume(this.playVolume / 100);
        break;
      case "openHomeMenu":
        this.setvideoTimeOut();
        this.menu.open();
        break;
    }

    this.ref.detectChanges();
  }

