import { Component, Input, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { ChannelProvider } from "../../providers/channel/channel";
import { ExtendedUrlImg } from "../../extended/extendedurlimg";
import { DeviceConst } from "../../interfaces/deviceconst";
import { NavController } from "ionic-angular";

/**
 * Generated class for the DoorlockComponent component.
 *
 * See https://angular.io/docs/ts/latest/api/core/index/ComponentMetadata-class.html
 * for more info on Angular Components.
 */
@Component({
  selector: 'doorlock-device',
  templateUrl: 'doorlock.html'
})
export class DoorlockComponent implements OnInit, OnDestroy {

  //设备信息
  deviceinfo: any;
  onlinestatus: string;
  src: string = "";
  type: string = "";
  lockLowPower: any;
  isgroup: boolean = false;//是否是组设备
  locklowpower: boolean = false;//锁是否低电量
  constructor(
    private navCtrl: NavController,
    private ref: ChangeDetectorRef,
    private ch: ChannelProvider,
    private extimg: ExtendedUrlImg) {
  }
  ngOnInit() {
    this.ref.markForCheck();
    this.lockLowPower = this.ch.sub("lockLowPower").subscribe(data => {
      if (data.data.device_id === this.deviceinfo.device_id) {
        this.locklowpower = data.data.services;
        this.ref.detectChanges();
      }
    });
  }

  ngOnDestroy() {
    this.ref.detach();
    this.lockLowPower.unsubscribe();
  }

  @Input() set device(deviceinfo: any) {
    this.deviceinfo = deviceinfo;
    this.isgroup = this.deviceinfo.ingroup === 2;
    this.src = this.extimg.getlist_64(this.deviceinfo.product_id);
    this.deviceTypeJudgment(0);
  }
  @Input() set Type(type: string) {
    this.type = type;
  }

  deviceTypeJudgment(operation: number) {
    switch (this.deviceinfo.product_id) {
      case DeviceConst.DOOR:
        if (operation == 0)
          this.onlinestatus = (this.deviceinfo.device_door_rf.magnet_device_id == null || this.deviceinfo.device_door_rf.magnet_device_id == "") ? "未绑定门磁" : "已绑定门磁";
        else if (operation == 1)
          this.navCtrl.push('doorlock', { 'device_id': this.deviceinfo.device_id });
        break;
      case DeviceConst.DOOR_MAGNET:
        if (operation == 0)
          this.onlinestatus = "";
        else if (operation == 1)
          this.navCtrl.push('doormagnet', { 'device_id': this.deviceinfo.device_id });
        break;
    }
  }

  pushPage() {
    this.deviceTypeJudgment(1);
  }
}



