import { Component, Input } from '@angular/core';

@Component({
    selector: "button-curtain-template",
    templateUrl: "./churtain.template.component.html",
})
//开关按钮样式状态
export class CurtainButtonComponent {
    // outline ion-button icon-only
    round_switch_button_status: string;
    @Input() set cmyroundstatus(roundstatus: string) {
        this.round_switch_button_status = roundstatus;
    }
}


